<template>
  <div>
	  <head-top></head-top>
	   <el-row style="margin-top: 20px;">
  			<el-col :span="12" :offset="4">
		        <el-form  label-width="110px" class="demo-formData">
					<el-form-item label="" prop="name">
						<el-input v-model="name"></el-input>
					</el-form-item>
					<el-form-item label="" prop="phone">
        				<el-input placeholder="" v-model="pwd" show-password></el-input>
					</el-form-item>
					<el-form-item label="" prop="description">
        				<el-input placeholder="" v-model="repwd" show-password></el-input>
					</el-form-item>
        			<p v-text="data"></p>
					<el-form-item label="" prop="promotion_info">
				        <el-input placeholder="" v-model="phone"></el-input>
					</el-form-item>
					<el-form-item label="1" prop="promotion_info">
        				<el-input placeholder="1" v-model="carid1" ></el-input>
					</el-form-item>
					<el-form-item label="2" prop="promotion_info">
        				<el-input placeholder="2" v-model="carid2" ></el-input>
					</el-form-item>
					<el-form-item label="3" prop="promotion_info">
        				<el-input placeholder="3" v-model="carid3" ></el-input>
					</el-form-item>
					<el-form-item label="4" prop="promotion_info">
        				<el-input placeholder="4" v-model="carid4" ></el-input>
					</el-form-item>
		        </el-form>
  			</el-col>
	   	</el-row>
		<div class="btn">
			<el-button round @click="addCar"></el-button>
		</div>
    	
	   
  </div>
</template>

<script>
import axios from 'axios'
import HeadTop from '../components/HeadTop'
export default {
	components:{
		HeadTop
	},
	data() {
		return {
			data:"",
			name:'',
			pwd:'',
			repwd:'',
			phone:'',
			carid1:"",
			carid2:"",
			carid3:"",
			carid4:""
		}
	},
	methods: {
		addCar() {
			if(this.pwd != this.repwd){
                this.data = ""
                return false
			}
            var formData = new window.FormData() // vue  window.FormData(), 'FormData isn't definded'
			formData.append('name',this.name)
			formData.append('pwd',this.pwd)
			formData.append('phone',this.phone)
			let carid = this.carid1+this.carid2+this.carid3+this.carid4
			formData.append('carid',carid)
			axios.post("http://47.94.38.46:80/home/home/userCarSave1",formData,{
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Accept': 'application/json'
                }
            }).then((res)=>{
                    // console.log(res)
                    // alert("")
					 this.$router.push('/userlist');
					 location.reload()
            }).catch((err)=>{
				console.log(err)
				alert("")
            })
		}
	},
}
</script>

<style>
.btn{
	display: flex;
}
.btn div{
	flex: 0.4;
	margin-right: 9%
}

</style>